import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProblemaService } from '../services/problemas.service';
import { CompetenciaService } from '../services/competencia.service';
import * as firebase from 'firebase';
import { Observable } from 'rxjs';
import { AlertController } from '@ionic/angular';
import { Competencia } from '../model/competencia';
import { isoStringToDate } from '@angular/common/src/i18n/format_date';
import { Modalidade } from '../model/modalidade';
import { UsuarioService } from '../services/usuario.service';
import { Usuario } from '../model/usuario';
import { Problema } from '../model/problema';
import { DISABLED } from '@angular/forms/src/model';
import * as confetti from 'canvas-confetti';
import { Medalha } from '../model/medalha';

@Component({
  selector: 'app-problema',
  templateUrl: './problema.page.html',
  styleUrls: ['./problema.page.scss'],
})
export class ProblemaPage implements OnInit {
  private problema_respondido = false
  private id;
  private competencia_id;
  private competencia;
  private problema;
  private problemas_da_competencia = [];
  private modalidade_id;
  private modalidade;
  private competencias_da_modalidade;
  private dica;
  private respostaCorreta;
  private afs = firebase.firestore();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private problemaService: ProblemaService,
    private alertController: AlertController,
    private usuarioService: UsuarioService
  ) { }

  ngOnInit() {
    let isso = this
    this.id = this.route.snapshot.params['id']
    this.competencia_id = this.route.snapshot.params['comp']

    let competencia = new Competencia()
    competencia.getCompetencia(this.competencia_id).then(competencia => {
      isso.competencia = competencia
      this.problemas_da_competencia = competencia['problemas'].split(", ")
    })

    this.getProblema(this.id)

    //verificar se o problema foi respondido
    let problemas_respondidos
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        isso.usuarioService.getUsuario(user.uid).then(snapshot => {
          problemas_respondidos = snapshot.problemas_respondidos.split(', ')
          problemas_respondidos.forEach(item => {
            if (item == isso.id) {
              //desativar botoes e colorir de verde
              isso.problema_respondido = true
              let botoes = document.getElementsByClassName("buttonBorda")
              var botao = <HTMLInputElement>document.getElementById("buttonEliminaIncorreta");
              botao.disabled = true;

              for (let item in botoes) {
                if (botoes[item].id != isso.respostaCorreta && typeof (botoes[item].id) == "string") {
                  //botÃµes errados
                  let disabled = document.createAttribute('disabled');
                  disabled.value = "true";
                  botoes[item].attributes.setNamedItem(disabled)
                  botoes[item].setAttribute("color", "danger");
                  
                } else if(botoes[item].id == isso.respostaCorreta && typeof (botoes[item].id) == "string") {
                  //botÃ£o certo
                  let disabled = document.createAttribute('disabled');
                  disabled.value = "true";
                  botoes[item].attributes.setNamedItem(disabled);
                  
                  botoes[item].setAttribute("color", "success");
                }
              }
            }
          })
        })
      }
    });
    //

    this.modalidade_id = this.route.snapshot.params['mod']
    let modalidade = new Modalidade()
    modalidade.getModalidade(this.modalidade_id).then(modalidade => {
      isso.modalidade = modalidade
      isso.competencias_da_modalidade = modalidade['competencias'].split(", ")
    })
  }

  //Exibe dica em um botÃ£o alert
  async exibeDica() {
    const alert = await this.alertController.create({
      header: 'Dica',
      message: this.dica,
      buttons: ['OK']
    });
    await alert.present();
  }

  //Corrige resposta, recebendo como parÃ¢metro uma string correspondente Ã  alternativa selecionada no HTML
  corrigeResposta(identificador) {
    let this1 = this

    if (identificador == this.respostaCorreta) {
      //salva problema respondido no usuario
      firebase.auth().onAuthStateChanged(async function (user) {
        if (user) {
          let usuario = new Usuario();
          usuario.setProblemaRespondido(user.uid, this1.id); 

          let problema = new Problema();
          problema.getProblema(this1.id).then(async problema => {
            await usuario.somarPontuacao(problema.pontuacao);
            this1.verificarMedalha();
          });

        }
      });

      

      let ultimo = false
      let ultimaCompetencia = false

      for (let i = 0; i < this.problemas_da_competencia.length; i++) {
        if (i == (this.problemas_da_competencia.length - 1) && this.problemas_da_competencia[i] == this.id) {
          for (let j = 0; j < this.competencias_da_modalidade.length; j++) {
            if (j == this.competencias_da_modalidade.length - 1 && this.competencia_id == this.competencias_da_modalidade[j]) {
              ultimaCompetencia = true
              this.alertUltimaCompetencia();
            }
          }
          ultimo = true
          if (ultimaCompetencia == false) this.alertUltimoProblema()
        }
      }
      if (ultimo == false) {
        let problema = new Problema();
        problema.getProblema(this1.id).then(problema => {
          this.alertAcerto(problema.pontuacao)
        });
        
      } 
    } else {
      this.alertErro();
    }
  }

  verificarMedalha() {
    let this1 = this;
    let medalha = new Medalha();
    medalha.getMedalhas().then( medalhas => {
      let usuario = new Usuario();

        medalhas.forEach( (medalha) => {
         usuario.getUsuario(firebase.auth().currentUser.uid).then(async (user) => {
          let pontuacao = user.pontuacao;
          let quantidade_problemas_respondidos = user.quantidade_problemas_respondidos;
          let medalhas_usuario = user.medalhas.split(", ");
          let medalha_existe = false;

          await medalhas_usuario.forEach(medalha_usuario => {
            if (medalha.codigo == medalha_usuario) {
              medalha_existe = true;
            }
          });

          if (!medalha_existe) {

            if (medalha.isPontuacao == true) {

              if (pontuacao >= medalha.condicao) {
                this1.alertMedalhaPontuacao(medalha);
                await usuario.addMedalhaUsuario(user.codigo, medalha.codigo);
              }

            } else {

              if (quantidade_problemas_respondidos >= medalha.condicao) {
                this1.alertMedalhaQtdProblemas(medalha);
                await usuario.addMedalhaUsuario(user.codigo, medalha.codigo);
              }
            }

          }
        });

      });
    });
  }

  
  //Alert para medalhas de pontuaÃ§Ã£o
  async alertMedalhaPontuacao(medalha) {
    const alert = await this.alertController.create({
      header:`VocÃª desbloqueou uma nova medalha!`,
      message: `
      <div class="medalhaAlertCorpo">
          <div> VocÃª alcanÃ§ou ${medalha.condicao} pontos! Vamos comemorar!</div>
          <br>
          <img class="medalhaAlertImagem" src="${medalha.imagem}" alt="imagem da medalha" style="border-radius: 2px">
          <br>
          <div class= "medalhaAlertTitulo"> ${medalha.titulo} </div>
          <div class="medalhaAlertDescricao"> ${medalha.descricao} </div>
          <br/>
      </div>
        `,
      buttons: ["OK"]
    });
    await alert.present();
  }

//Alert para medalhas de quantidade de problemas respondidos
async alertMedalhaQtdProblemas(medalha) {
  const alert = await this.alertController.create({
    header:`VocÃª desbloqueou uma nova medalha!`,
    message: `
    <div class="medalhaAlertCorpo">
        <div> VocÃª jÃ¡ respondeu ${medalha.condicao} problema(s)! Vamos comemorar!</div>
        <br>
        <img class="medalhaAlertImagem" src="${medalha.imagem}" alt="imagem da medalha" style="border-radius: 2px">
        <br>
        <div class= "medalhaAlertTitulo"> ${medalha.titulo} </div>
        <div class="medalhaAlertDescricao"> ${medalha.descricao} </div>
        <br/>
    </div>
      `,
    buttons: ["OK"]
  });
  await alert.present();
}

  //Alert de RESPOSTA CORRETA
  async alertAcerto(pontuacao) {
    const alert = await this.alertController.create({
      header: 'VOCÃŠ ACERTOU!',
      message: 'ParabÃ©ns, vocÃª recebeu ' + pontuacao + " pontos!",
      buttons: [
        {
          text: 'PrÃ³ximo problema',
          role: 'next',
          handler: () => {
            let isso = this

            for (let i = 0; i < this.problemas_da_competencia.length; i++) {
              if (isso.id == this.problemas_da_competencia[i]) {
                this.router.navigate(['/problema', isso.modalidade_id, isso.competencia_id, this.problemas_da_competencia[i + 1]]).then(nav => {
                  window.location.reload();
                });
              }
            }

          }
        }
      ]
    });
   
    //Desativa botÃµes de alternativas
    let botoes = document.getElementsByClassName("buttonBorda")
    for (let item in botoes) {
      if (botoes[item].id != this.respostaCorreta && typeof (botoes[item].id) == "string") {
        //botÃµes errados
        let disabled = document.createAttribute('disabled');
        disabled.value = "true";
        botoes[item].attributes.setNamedItem(disabled)
        botoes[item].setAttribute("color", "danger");
        
      } else if(botoes[item].id == this.respostaCorreta && typeof (botoes[item].id) == "string") {
        //botÃ£o certo
        let disabled = document.createAttribute('disabled');
        disabled.value = "true";
        botoes[item].attributes.setNamedItem(disabled);
        
        botoes[item].setAttribute("color", "success");
      }
    }
    
    this.confete(); //Solta confete
    await alert.present();
  }

  async alertUltimoProblema() {
    const alert = await this.alertController.create({
      header: 'ParabÃ©ns!',
      message: 'VocÃª concluiu a CompetÃªncia ' + this.competencia.nome +"!",
      buttons: [
        {
          text: 'PrÃ³xima CompetÃªncia',
          role: 'next',
          handler: () => {
            console.log('Proxima competencia')
            for (let i = 0; i < this.competencias_da_modalidade.length; i++) {

              if (this.competencias_da_modalidade[i] == this.competencia_id) {
                this.router.navigate(['/lista-problemas', this.modalidade_id, this.competencias_da_modalidade[i + 1]]).then(nav => {
                  window.location.reload();
                });
              }

            }
          }
        }
      ]
    });
    this.confete();
    await alert.present();
  }

  //Alert de RESPOSTA INCORRETA
  async alertErro() {
    const alert = await this.alertController.create({
      header: 'RESPOSTA ERRADA!',
      message: 'VocÃª errou a resposta! :( <br>Mas nÃ£o desanime! VocÃª tem outra chance! <br> Lembre-se que vocÃª ainda pode usar os poderes de Wally: exibir dica ou eliminar uma resposta incorreta.',
      buttons: [
        {
          text: 'Tentar novamente'
        }, {
          text: 'Exibir Dica',
          handler: () => {
            this.exibeDica();
          }
        }
      ]
    });
    await alert.present();
  }

  async alertUltimaCompetencia() {
    const alert = await this.alertController.create({
      header: 'ParabÃ©ns!',
      message: 'VocÃª concluiu a ' + this.modalidade.nome +"!",
      buttons: [{
        text: 'Voltar para Modalidades',
        role: 'next',
        handler: () => {
          this.router.navigate(['/modalidades']).then(param => {
            window.location.reload();
          });
        }
      }]
    });
    this.confete();
    await alert.present();
  }


  //Elimina uma das respostas incorretas
  eliminaIncorreta() {
    let alternativas = ['a', 'b', 'c', 'd'];
    let correta: number = alternativas.indexOf(this.respostaCorreta);

    alternativas.splice(correta, 1);//Remove resposta correta do array

    let eliminada = Math.floor(Math.random() * 3);//Gera nÃºmero aleatÃ³rio entre 0 e 2 (Ã­ndice entre as 3 alternativas erradas)
    console.log("A alternativa eliminada foi a letra " + alternativas[eliminada]);

    var botao = <HTMLInputElement>document.getElementById("buttonEliminaIncorreta");
    botao.disabled = true;

    var botaoEliminada = <HTMLInputElement>document.getElementById(alternativas[eliminada]);
    botaoEliminada.disabled = true;
    botaoEliminada.setAttribute("color", "danger");
    this.alertAltRemovida(alternativas[eliminada]);
  }

  //Alert que mostra qual das alternativas foi eliminada
  async alertAltRemovida(alt) {
    const alert = await this.alertController.create({
      header: 'Wally mandou dizer que...',
      message: 'A alternativa (' + alt + ') estÃ¡ incorreta! <br> Ainda restam trÃªs alternativas para vocÃª escolher.',
      buttons: ['OK']
    });
    await alert.present();
  }

  //AnimaÃ§Ã£o de confetti
  confete(){
     confetti.create(undefined, { resize: true, useWorker: false })({
      shapes: ['circle', 'circle', 'square'],
      particleCount: 400,
      spread: 90,
      gravity: 2,
      origin: {
        y: (0),
        x: (1)
      }
    });
  }

  getProblema(codigo: string) {
    let agora = this
    firebase.database().ref('/problemas/' + codigo).once('value').then(function (snapshot) {
      agora.mudarHtml(snapshot.val())
    });
  }

  mudarHtml(data) {
    // document.getElementById('titulo').innerHTML = data['titulo']
    document.getElementById('teste').innerHTML = data['titulo']
    document.getElementById('enunciado').innerHTML = data['enunciado']

    document.getElementById('a').innerHTML = data['alternativaA']
    document.getElementById('b').innerHTML = data['alternativaB']
    document.getElementById('c').innerHTML = data['alternativaC']
    document.getElementById('d').innerHTML = data['alternativaD']

    this.dica = data['dica'];
    this.respostaCorreta = data['alternativaCorreta'];

    let imgCodigo = document.getElementById('programador')
    imgCodigo['src'] = data['imagemCodigo']
    let imgContexto = document.getElementById('contexto')
    imgContexto['src'] = data['imagem']

  }


  //ConfiguraÃ§Ãµes do slide de imagens (contexto e cÃ³digo do programador)
  slideOpts = {
    on: {
      beforeInit() {
        const swiper = this;
        swiper.classNames.push(`${swiper.params.containerModifierClass}flip`);
        swiper.classNames.push(`${swiper.params.containerModifierClass}3d`);
        const overwriteParams = {
          slidesPerView: 1,
          slidesPerColumn: 1,
          slidesPerGroup: 1,
          watchSlidesProgress: true,
          spaceBetween: 0,
          virtualTranslate: true,
        };
        swiper.params = Object.assign(swiper.params, overwriteParams);
        swiper.originalParams = Object.assign(swiper.originalParams, overwriteParams);
      },
      setTranslate() {
        const swiper = this;
        const { $, slides, rtlTranslate: rtl } = swiper;
        for (let i = 0; i < slides.length; i += 1) {
          const $slideEl = slides.eq(i);
          let progress = $slideEl[0].progress;
          if (swiper.params.flipEffect.limitRotation) {
            progress = Math.max(Math.min($slideEl[0].progress, 1), -1);
          }
          const offset$$1 = $slideEl[0].swiperSlideOffset;
          const rotate = -180 * progress;
          let rotateY = rotate;
          let rotateX = 0;
          let tx = -offset$$1;
          let ty = 0;
          if (!swiper.isHorizontal()) {
            ty = tx;
            tx = 0;
            rotateX = -rotateY;
            rotateY = 0;
          } else if (rtl) {
            rotateY = -rotateY;
          }

          $slideEl[0].style.zIndex = -Math.abs(Math.round(progress)) + slides.length;

          if (swiper.params.flipEffect.slideShadows) {
            // Set shadows
            let shadowBefore = swiper.isHorizontal() ? $slideEl.find('.swiper-slide-shadow-left') : $slideEl.find('.swiper-slide-shadow-top');
            let shadowAfter = swiper.isHorizontal() ? $slideEl.find('.swiper-slide-shadow-right') : $slideEl.find('.swiper-slide-shadow-bottom');
            if (shadowBefore.length === 0) {
              shadowBefore = swiper.$(`<div class="swiper-slide-shadow-${swiper.isHorizontal() ? 'left' : 'top'}"></div>`);
              $slideEl.append(shadowBefore);
            }
            if (shadowAfter.length === 0) {
              shadowAfter = swiper.$(`<div class="swiper-slide-shadow-${swiper.isHorizontal() ? 'right' : 'bottom'}"></div>`);
              $slideEl.append(shadowAfter);
            }
            if (shadowBefore.length) shadowBefore[0].style.opacity = Math.max(-progress, 0);
            if (shadowAfter.length) shadowAfter[0].style.opacity = Math.max(progress, 0);
          }
          $slideEl
            .transform(`translate3d(${tx}px, ${ty}px, 0px) rotateX(${rotateX}deg) rotateY(${rotateY}deg)`);
        }
      },
      setTransition(duration) {
        const swiper = this;
        const { slides, activeIndex, $wrapperEl } = swiper;
        slides
          .transition(duration)
          .find('.swiper-slide-shadow-top, .swiper-slide-shadow-right, .swiper-slide-shadow-bottom, .swiper-slide-shadow-left')
          .transition(duration);
        if (swiper.params.virtualTranslate && duration !== 0) {
          let eventTriggered = false;
          // eslint-disable-next-line
          slides.eq(activeIndex).transitionEnd(function onTransitionEnd() {
            if (eventTriggered) return;
            if (!swiper || swiper.destroyed) return;

            eventTriggered = true;
            swiper.animating = false;
            const triggerEvents = ['webkitTransitionEnd', 'transitionend'];
            for (let i = 0; i < triggerEvents.length; i += 1) {
              $wrapperEl.trigger(triggerEvents[i]);
            }
          });
        }
      }
    }
  };


}
